import React,{Component} from 'react';
import './Header.css';
import { Redirect } from 'react-router-dom';
import LoginStore from './auth/LoginStore.js'
import LoginForm from './auth/LoginForm.js'

class Header extends Component {
	constructor(props){
		super(props)
		
		this.state = {redirect:false,isLoggedIn:LoginStore.isLoggedIn(),overlay:"none",loginDisplay:"none",registerDisplay:"none"}
		this.clickOverlay = this.clickOverlay.bind(this)
	}
	
	clickOverlay(){
		this.setState({overlay:"none",loginDisplay:"none",registerDisplay:"none"})

	}
	
	componentDidMount(){

		LoginStore.on("storeChange", this.updateData);
	}
	componentWillUnmount(){
		LoginStore.removeListener("storeChange", this.updateData);
	}
	updateData = () => {
		this.setState({isLoggedIn : LoginStore.isLoggedIn()});
	};

	ifLoggedIn(){
		if(this.state.isLoggedIn == true){
			return(<div className="navigation"><div className="userinfo"> Hello, {LoginStore.getUser().login}</div><div className="userinfo"><a href="/auth/logout">Logout</a></div></div>)
			
		}else{
			return(<div className="navigation"><div className="button2" onClick={() => this.setState({loginDisplay:"block"})}>Login</div><div className="button2" onClick={() => this.setState({registerDisplay:"block"})}>Regsiter</div></div>)
		}
	}
	
	render(){
		return(
			<div className="componentHead">
				<div className="Header">
					<a href="/home"><div className="logo-name">FitSE .com</div></a>
					{this.ifLoggedIn()}
					
					
					
					<div id="overlay" style={{display:this.state.overlay}} onClick={() => this.clickOverlay()}></div>
				</div>
				<LoginForm display={this.state.loginDisplay} clickoverlay={this.clickOverlay}/>
			</div>
			
			
		)
	}
}

export default Header;
