import React,{Component} from 'react';
import './Home.css';
import LoginStore from './auth/LoginStore.js'
import Header from './Header.js'
import BuyPanel from './BuyPanel.js'
import Deposit from './Deposit.js'


class Home extends Component {
	constructor(props){
		super(props)
		this.state = {user : LoginStore.getUser(),jwt : LoginStore.getJwt(),isLoggedIn : LoginStore.isLoggedIn(),buypanelDisplay:"none",overlay:"none",depositDisplay:"none"}
	}
	
	componentDidMount(){
		LoginStore.on("storeChange", this.updateData);
		if (this.state.isLoggedIn){
			
		}
		this.loadComments()
	}
	
	componentWillUnmount(){
		LoginStore.removeListener("storeChange", this.updateData);
	}
	
	clickOverlay(){
		this.setState({overlay:"none",buypanelDisplay:"none",depositDisplay:"none"})
		
	}
	
	
	updateData = () => {
		this.setState({user : LoginStore.getUser(),jwt : LoginStore.getJwt(),isLoggedIn : LoginStore.isLoggedIn()});
		if(LoginStore.isLoggedIn() == false){
			this.state.redirect = true;
		}
		
	};
	
	clickLoginTab(){
		if(this.HeadRef.state.loginDisplay == "none") this.HeadRef.setState({loginDisplay:"block"})
	}
	
	
	showIfLogged(boole){
		if(boole == true){
			if(this.state.isLoggedIn == true) return("inline-block")
			return("none")
	
		}
		if(boole == false){
			if(this.state.isLoggedIn == true) return("none")
			return("inline-block")
	
		}

	}
	
	renderWhenLogged(){
		if(this.state.isLoggedIn == true){
			return(<div className="userInformation">Username: {this.state.user.login}<br />Wallet: {this.state.user.wallet} $<br />Accounts Bought: {this.state.user.bought}<br /></div>)
		}
	}
	
	loadComments(){
		fetch("http://localhost:3001/reviews").then(res => res.json()).then(res => {
			let comments = res.data.map(key => {return(<div className="comment"><div className="name">{key.nick}</div>{key.comment}</div>)})
			this.setState({comments:comments})
				
			
		})
		
	}
	
	

	
	render(){
		return(
			<div className="Home">
				<BuyPanel display={this.state.buypanelDisplay}/>
				<Deposit display={this.state.depositDisplay}/>
			
				<Header ref={(ref) => (this.HeadRef = ref)}/>
				<div className="ControlMenu">
					<div className="ControlTab">
						Accounts Stock
						<div className="ControlTabInfo">
							142
						</div>
					</div>
					
					<div className="ControlTab">
						Account Price
						<div className="ControlTabInfo">
							10$
						</div>
					</div>
					
					<div className="ControlTab">
						Delivery Time
						<div className="ControlTabInfo">
							Instant
						</div>
					</div>
				</div>
				
				{this.renderWhenLogged()}
				<br />
				<div className="productList">
					<div className="tab" style={{display:this.showIfLogged(false)}} onClick={() => this.clickLoginTab()}>
						<div className="offertText">Login</div>
						<img src={require('./img/register.png')}></img>
					</div>
					<div className="tab" style={{display:this.showIfLogged(false)}}>
						<div className="offertText">Register</div>
						<img src={require('./img/register.png')}></img>
					</div>
					<div className="tab"  onClick={() => {this.setState({depositDisplay:"block",overlay:"block"})}}>
						<div className="offertText">Deposit Money</div>
						<img src={require('./img/addmoney.png')}></img>
					</div>
					<div className="tab" onClick={() => {this.setState({buypanelDisplay:"block",overlay:"block"})}}>
						<div className="offertText">Buy Fitbit Accounts</div>
						<img src={require('./img/buyacc.png')}></img>
					</div>
					
					
				</div>
				
				<div className="reviews">
				Client Reviews:


				
				
				</div>
				
				
				<div id="overlay" style={{display:this.state.overlay}} onClick={() => this.clickOverlay()}></div>
			</div>
		)
	}
}

export default Home;
