import React, {Component} from 'react';
import AuthActions from './AuthActions.js';
import { Redirect } from 'react-router-dom';
import './App.css'
import LoginStore from './LoginStore.js'
import jwt_decode from "jwt-decode";

class LoginForm extends Component{
	constructor(props){
		super(props)
		this.inputDefault = [{key:0,isfocus:0,value:"Login",type:"text",minchar:3},{key:1,isfocus:0,value:"Password",type:"text",minchar:3}]
		this.inputStance = [{key:0,isfocus:0,value:"Login",type:"text"},{key:1,isfocus:0,value:"Password",type:"text"}]
		this.responseList = ["","You have to fill in all fields","You have to use at least 8 characters for Login/Password","Incorrect login or password"]
		this.state = {value: 0,redirect: false,props:this.props}
		this.handleChange = this.handleChange.bind(this);
		
		this.isLoggedIn()
	}
	
	componentWillReceiveProps(NextProps){
		this.setState({props:NextProps})
	}
	
	isLoggedIn(){
		if(LoginStore.isLoggedIn() == true) {
			this.state.redirect = true
			this.setState({redirect:true})
		}
			
	}
	
	_onFocus = key => {
		let newVal = this.inputStance.slice();
		if (newVal[key].value === this.inputDefault[key].value)
		{
			newVal[key].value = ""
		}
		if (key === 1)
		{
			newVal[key].type = "password"
		}
		this.setState({inputDefault : newVal})
	}
	
	_onBlur = key => {
		let newVal = this.inputStance.slice();
		let defaultValue = this.inputDefault[key].value
		if (newVal[key].value === "")
		{
			newVal[key].value = defaultValue
		}
		if (key === 1 && newVal[key].value === defaultValue)
		{
			newVal[key].type = "text"
		}
		this.setState({inputStance : newVal})
	}
	
	handleChange(event){
		let liveValue = this.inputStance.slice()
		let key = event.target.getAttribute('data-key')
		liveValue[key].value = event.target.value
		this.setState({state : liveValue});
	}
	
	handleSubmit = () => {
		let loginValue = this.inputStance[0].value
		this.setState({value : 0})
		let JSresponse = this.inputStance.map((value,key,minchar) => {
			if (value.value === this.inputDefault[value.key].value)
			{
				this.setState({value : 1})
				return 1
			}
			if (value.value.length < this.inputDefault[value.key].minchar)
			{
				this.setState({value : 2})
				return 2
			}
			return 0
		})
		if (JSresponse[0] > 0){
			return 1
		}

		let passwordValue = this.inputStance[1].value
		fetch('http://localhost:3001/login',{method: 'post', mode: 'cors', headers: {'Content-Type':'application/json','Accept': 'application/json'},body: JSON.stringify({"login":loginValue,"password":passwordValue})})
		.then(response => response.json(),error => console.log("An error occured.". error))
		.then(res => {
			if (res.response === 1){
				AuthActions(res.token);
				this.setState({redirect : true});
				this.props.clickoverlay()
			}else if (res.response === 0){
				this.setState({value : 3})
				return 3
				
			}
			});
		
		
		
	}
	
	renderRedirect(){
		if (this.state.redirect){
			return <Redirect to='/home' />
		}
	}
	
	_handleKeyDown = (e) => {
		if (e.key === "Enter"){
			e.preventDefault();
			this.handleSubmit();
		}
	}
	
	clickoverlay(){
		this.props.clickoverlay()
	}

	
	render(){
		return(
			<div className="containerLogin" style={{display:this.state.props.display}}>
				{this.renderRedirect()}
				<div className="containerMenu">
					<p>Log in<div className="closebtn" onClick={() => this.clickoverlay()}>X</div></p>
					<div className="Form">
					
						<div className="inf">{this.responseList[this.state.value]}</div>
						<input className="label" data-key={this.inputStance[0].key} onKeyDown={this._handleKeyDown} onChange={this.handleChange} onBlur={() => this._onBlur(this.inputStance[0].key)} onFocus={() => this._onFocus(this.inputStance[0].key)} type="text" spellCheck="false" name="login" value={this.inputStance[0].value} />
						<br /><br />
						<input className="label" data-key={this.inputStance[1].key} onKeyDown={this._handleKeyDown} onChange={this.handleChange} onBlur={() => this._onBlur(this.inputStance[1].key)} onFocus={() => this._onFocus(this.inputStance[1].key)} type={this.inputStance[1].type} spellCheck="false" name="password" value={this.inputStance[1].value} />
						<br /><br /><br />
						<button className="btn btn-info" onClick={() => this.handleSubmit()}>Login</button>
					</div>
				
				</div>
			</div>
		)
		
		
	}
	

}

export default LoginForm;