import React,{Component} from 'react';
import './BuyPanel.css';
import Slider from '@material-ui/core/Slider';

class BuyPanel extends Component {
	constructor(props){
		super(props)
		this.state = {slidervalue:1,price:10}
		this.handleChange = this.handleChange.bind(this)

	}
	
	handleChange = (e,value) => {
		if(value == 0) value = 1
		this.setState({slidervalue:value})
		
	}
  
	render(){
		return(
			<div className="BuyPanel" style={{display:this.props.display}}>

				<div className="title">Make a order</div>
				<div className="label1"></div>
				
				<br /><span className="qty">Account quantity: {this.state.slidervalue}</span><br />
				<Slider style={{width:"200px"}} value={this.state.slidervalue} max="20" onChange={this.handleChange} aria-labelledby="discrete-slider-small-steps" />			
				<br />
				<span className="price">Price: {this.state.slidervalue * this.state.price} $</span>
				

				<div className="buttonOrder">Order Now</div>
				
			</div>
			
			
		)
	}
}

export default BuyPanel;
