import React,{Component} from 'react';
import './App.css';
import Header from './Header.js'
import Home from './Home.js'
import {BrowserRouter as Router,Switch,Route,Link,Redirect} from "react-router-dom";
import {HashRouter} from "react-router-dom";
import LoginForm from './auth/LoginForm.js'
import Dispatcher from './auth/Dispatcher.js';



class Logout extends Component{
	
	logout(){
		localStorage.removeItem('jwt');
		Dispatcher.dispatch({
			actionType: 'USER_LOGOUT'
		})
	}
	componentDidMount(){
		this.logout()
		
	}
	render(){
		return(
			<div className="logout"><Redirect to="/home" /></div>
		)
	}
}


class App extends Component {

	constructor(props){
		super(props)
		this.state = {}
		this.HeadRef = React.createRef()
	}


	render(){

		return(
		
		
			<div className="App">

				<Router basename={process.env.PUBLIC_URL +"/"}>
					<Switch>
						<Route exact name="home" path="/" component={Home} />
						<Route exact name="home" path="/home" component={Home} />
						<Route exact name="login" path="/auth/login" component={LoginForm} />
						<Route exact name="logout" path="/auth/logout" component={Logout} />
					</Switch>
				</Router>
		
	
			</div>
		)
	}
}

export default App;
